import NumberKeyboard from './VueKeyboard.vue' // 导入组件

const VueKeyboard = {
 install: function (Vue) {
 if (typeof window !== 'undefined' && window.Vue) {
  Vue = window.Vue
 }
 Vue.component('VueKeyboard', NumberKeyboard)
 }
}
 
export default VueKeyboard



